<template>
  <div class="nav-bar">
    <ul class="nav-list">
      <li :class="{active:currentIndex === index + 1}" ref="liitem" v-for="(item,index) in List" :key="index" @click="navClick(index)">{{item}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name:'Nav-bar',
  data () {
    return {
      Liitem:{},  
    }
  },
  props: {
    List:{
      type:Array,
    },
    currentIndex:{
      type:Number
    }
  },
  methods: {
    navClick(index){
      this.$emit('navClick',index);
      console.log(index)
      console.log(this.currentIndex)
    },
    handleDown(){
      this.Liitem = this.$refs.liitem
    }
  },
  mounted () {
    this.handleDown()
  },
}
</script>

<style scoped>
.nav-bar{
  width: 100%;
  height: 49px;
  background-color: #2c3e50;
  position: absolute;
  z-index: 4;
}
.nav-list{
  height: 100%;
  width: 100%;
  display: flex;
}
.nav-list > li{
  flex: 1;
  width: 25%;
  border-right: 1px solid #fff;
  box-sizing: border-box;
  color: #fff;
  font-size: 20px;
  line-height: 49px;
  text-align: center;
}
.nav-list li:hover{
  color: #e87e04;
}
.active{
  color: #e87e04;
}
.nav-list > li:last-child{
  border: 0;
}
</style>