<template>
  <div id="all-in">
    <div class="bg-skin"></div>
    <foucs :currentIndex="currentIndex" @itemClick="itemClick"></foucs>
    <nav-bar
      class="moveBar"
      v-show="isTabFixed"
      ref="navbar"
      :currentIndex="currentIndex"
      @navClick="navClick"
      :List="List"
    ></nav-bar>
    <div class="list" ref="list">
      <frist-page @abClick="abClick" @proClick="proClick"></frist-page>
      <nav-bar
        v-show="!isTabFixed"
        ref="navbar"
        :currentIndex="currentIndex"
        @navClick="navClick"
        :List="List"
      ></nav-bar>
      <two-page @navClick="navClick"></two-page>
      <three-page @scrollStop="scrollStop" @scrollRun="scrollRun"></three-page>
      <four-page></four-page>
    </div>
  </div>
</template>

<script>
import FristPage from "components/FristPage";
import TwoPage from "components/TwoPage";
import ThreePage from "components/ThreePage";
import FourPage from "components/FourPage";
import Foucs from "components/Focus";
import NavBar from "./childComp/Navbar";

import { setStyle, animate } from "components/SetStyle";

export default {
  name: "AllIn",
  data() {
    return {
      listEl: {},
      listStyle: {},
      currentIndex: 1,
      windomH: 0,
      scrollUp: 0,
      scrollDown: 0,
      List: ["首页", "关于我", "作品", "联系"],
      tabOffsetTop: 0,
      isTabFixed: false,
    };
  },
  components: {
    FristPage,
    TwoPage,
    ThreePage,
    FourPage,
    Foucs,
    NavBar,
  },
  methods: {
    //鼠标滚轮事件监听
    scrollFunc(e) {
      e = e || window.event;
      if (e.wheelDelta) {
        //判断浏览器IE，谷歌滑轮事件
        if (e.wheelDelta > 0) {
          //当滑轮向上滚动时
          this.scrollUp++;
          if (this.scrollUp === 2) {
            this.scrollUp = 0;
            if (this.currentIndex !== 1) {
              this.currentIndex--;
              this.upmove(this.currentIndex);
            }
          }
        }
        if (e.wheelDelta < 0) {
          //当滑轮向下滚动时
          this.scrollDown++;
          if (this.scrollDown === 2) {
            this.scrollDown = 0;
            if (this.currentIndex !== 4) {
              this.currentIndex++;
              this.downmove(this.currentIndex);
            }
          }
        }
      } else if (e.detail) {
        //Firefox滑轮事件
        if (e.detail > 0) {
          //当滑轮向上滚动时
          this.scrollUp++;
          if (this.scrollUp === 2) {
            this.scrollUp = 0;
            if (this.currentIndex !== 1) {
              this.currentIndex--;
              this.upmove(this.currentIndex);
            }
          }
        }
        if (e.detail < 0) {
          //当滑轮向下滚动时
          this.scrollDown++;
          if (this.scrollDown === 2) {
            this.scrollDown = 0;
            if (this.currentIndex !== 4) {
              this.currentIndex++;
              this.downmove(this.currentIndex);
            }
          }
        }
      }
    },
    //键盘按下事件处理
    keyDom(e) {
      var evt = e || event;
      this.upDown(evt.which);
    },
    //监听上下键的按下
    upDown(key) {
      if (key === 40) {
        if (this.currentIndex !== 4) {
          this.currentIndex++;
          this.downmove(this.currentIndex);
        }
      } else if (key === 38) {
        if (this.currentIndex !== 1) {
          this.currentIndex--;
          this.upmove(this.currentIndex);
        }
      }
    },
    downmove(index) {
      this.move(30, -(index - 1) * this.windomH);
    },
    upmove(index) {
      this.move(30, -(index - 1) * this.windomH);
    },
    haha(){
      if (this.tabOffsetTop <= -parseInt(this.listStyle.top)) {
        this.isTabFixed = true;
      } else {
        this.isTabFixed = false;
      }
    },
    move(topSize, top) {
      let inTop = parseInt(this.listStyle.top);
      if (parseInt(this.listStyle.top) > top) {
        clearInterval(timeid);
        let timeid = setInterval(() => {
          this.haha()
          this.setTop(parseInt(this.listStyle.top) - topSize);
          if (parseInt(this.listStyle.top) <= top) {
            this.listStyle.top = top + "px";
            clearInterval(timeid);
          }
        }, 10);
      } else if (parseInt(this.listStyle.top) < top) {
        clearInterval(timeid);
        let timeid = setInterval(() => {
          this.haha();
          this.setTop(parseInt(this.listStyle.top) + topSize);
          if (parseInt(this.listStyle.top) >= top) {
            this.listStyle.top = top + "px";
            clearInterval(timeid);
          }
        }, 10);
      }
    },
    setTop(position) {
      setStyle(this.listStyle, "top", position);
    },
    itemClick(index) {
      this.currentIndex = index;
      this.upmove(this.currentIndex);
    },
    abClick() {
      this.currentIndex++;
      this.upmove(this.currentIndex);
    },
    proClick() {
      this.currentIndex += 2;
      this.upmove(this.currentIndex);
    },
    scrollStop() {},
    scrollRun(e) {
      this.scrollFunc(e);
    },
    handleDown() {
      this.listEl = this.$refs.list;
      this.listStyle = this.listEl.style;
      this.listStyle.top = 0;
      //监听按下事件
      document.onkeydown = this.keyDom;

      this.windomH = window.innerHeight;
      //监听鼠标滚轮的滚动
      window.onmousewheel = document.onmousewheel = this.scrollRun;
      this.tabOffsetTop = this.$refs.navbar.$el.offsetTop;
    },
    navClick(index) {
      this.currentIndex = index + 1;
      this.upmove(this.currentIndex);
    },
  },
  mounted() {
    this.handleDown();
  },
};
</script>

<style>
@import url("~assets/iconfont/iconfont.css");
* {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
html,
body {
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-family: cursive;
}
#app {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.list {
  position: absolute;
  width: 100%;
  height: 100%;
}
.bg-skin {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #cccccc;
  opacity: 70%;
  z-index: 1;
}
.moveBar {
  position: fixed;
  top: 0;
}
</style>
