export function setStyle(elStyle, attr, position) {
  let deg = /deg/
  let px = /px/
  if (attr === 'transform') {
    if (deg.test(position)) {
      elStyle.transform = `rotate(${position})`;
      elStyle["-webkit-transform"] = `rotate(${position})`;
      elStyle["-ms-transform"] = `rotate(${position})`;
    } else if (px.test(position)) {
      elStyle.transform = `translate(${position})`;
      elStyle["-webkit-transform"] = `translate(${position})`;
      elStyle["-ms-transform"] = `translate(${position})`;
    }
    /* elStyle.transform = position;
    elStyle["-webkit-transform"] = position;
    elStyle["-ms-transform"] = position; */
  } else if (attr === 'opacity'){
    elStyle[attr] = `${position}`;
    elStyle["-webkit-" + attr] = `${position}`;
    elStyle["-ms-" + attr] = `${position}`;
  }else {
    elStyle[attr] = `${position}px`;
    elStyle["-webkit-" + attr] = `${position}px`;
    elStyle["-ms-" + attr] = `${position}px`;
  }
}
